  private getCurrentUrl(){
    let urlSliced = decodeURIComponent(window.location.pathname).slice(1);
    let path = (urlSliced === 'אולם-אירועים' || urlSliced === 'app/אולם-אירועים')? true : false; // || urlSliced === 'app/אולם-אירועים'
    
    this.urlUnsubscribe = this.router.events.pipe(
      filter(evt => evt instanceof RoutesRecognized),
      map(_evt => decodeURIComponent(_evt["url"]).slice(1)),
    )
    .subscribe(evt => {
      this.path = (evt === 'אולם-אירועים' || evt === 'app/אולם-אירועים') ? true : false;// || evt === 'app/אולם-אירועים'
      this.pathUrl = evt;
      // this.onSelectedLink();
    });
    this.path = path;
    console.log(urlSliced);
    this.pathUrl = urlSliced;
  }
