import { Injectable, EventEmitter } from '@angular/core';
import { HallType } from './hall-type';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, of, Subject, Observable, AsyncSubject, ReplaySubject } from 'rxjs';
import { tap,single, map, find, filter, first, pluck, take } from 'rxjs/operators';

@Injectable()
export class HallsDataService {

    private costumers: Observable<Object>;
    private activeProject:ReplaySubject<HallType> = new ReplaySubject(1);
    
    constructor(private http: HttpClient) { 
        this.costumers = this.http.get("http://ethio:8080/costumers");
    }
    
    public getAll() {
         
        return this.costumers;
    }

    public getById(costumerName: string) {
        if(costumerName === "ארמונות-לב") costumerName = costumerName.split('-')[0]+ " "+ costumerName.split('-')[1];
        
        this.costumers.
            subscribe(
                (evt) => {
                    for (let ii in evt) {
                        if (evt[ii].companyName === costumerName) {
                            this.activeProject.next(evt[ii]);
                        }
                    }
                }
            );
        return this.activeProject;
        
    }
}

