import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Router, NavigationEnd, NavigationStart, RoutesRecognized } from '@angular/router';
import { Location } from '@angular/common';
import { PagesService } from './pages-service';
import { UrlServiceService } from './url-service.service';
import { Observable, of, Subject, Subscription } from 'rxjs';

import { map, filter, tap, first, debounceTime, distinctUntilChanged, takeWhile } from 'rxjs/operators';
import * as $ from 'jquery';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent implements OnInit, OnDestroy {
  constHome: boolean = false;
  
  @Input() strUrl:boolean;

  constructor(
    private location: Location,
    private router: Router,
    ) {

    this.router.events
    .subscribe((_evt) => {
      this.constHome = this.location.path() === '' ? true : false;
      this.strUrl = this.constHome;
    });
  }

  ngOnInit() {
    
  }

  ngOnDestroy() {
    //this.uriObject.unsubscribe();
  }
}