import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

/****************** Sevices ********************/


/****************** MAIN Pages ********************/
import { WellcomeComponent } from './pages/wellcome/wellcome.component';
import { AboutComponent } from './pages/about/about.component';
import { ContactComponent } from './pages/contact/contact.component';
import { BlogComponent } from './pages/blog/blog.component';
import { GoalComponent } from './pages/goal/goal.component';

/****************** Costumer Component ********************/

import { HallsComponent } from "./pages/pro/halls/HallsComponent";
import { HallComponent } from './pages/pro/halls/hall/hall.component';
import { HallAboutComponent } from './pages/pro/halls/hall-about/hall-about.component';
import { HallMediaComponent } from './pages/pro/halls/hall-media/hall-media.component';
import { HallEditComponent } from './pages/pro/halls/hall-edit/hall-edit.component';

import { SalonsComponent } from './pages/pro/salons/salons.component';
import { SalonComponent } from './pages/pro/salons/salon/salon.component';
import { SalonMediaComponent } from './pages/pro/salons/salon-media/salon-media.component';
import { SalonAboutComponent } from './pages/pro/salons/salon-about/salon-about.component';

import { DjsComponent } from './pages/pro/djs/djs.component';

import { PhotograpersComponent } from './pages/pro/photograpers/photograpers.component';
import { PhotographerComponent } from './pages/pro/photograpers/photographer/photographer.component';
import { photographerAboutComponent } from './pages/pro/photograpers/about/photographerAbout.component';

import { HotelsComponent } from './pages/pro/hotels/hotels.component';

import { TransportationComponent } from './pages/pro/transportation/transportation.component';

import { RabbinateComponent } from './pages/pro/rabbinate/rabbinate.component';

import { CarRentComponent } from './pages/pro/car_rental/car-rent/car-rent.component';
import { CarRentAboutComponent } from './pages/pro/car_rental/car-rent-about/car-rent-about.component';

import { PrintingComponent } from './pages/pro/printing/printing.component';

import { CustomersComponent } from './pages/pro/customers.component';
import { PageNotFoundComponent } from './pages/page-not-found/page-not-found.component';
import { HallResolver } from './pages/pro/halls/halls-resolver.service';
import { ErrorPageComponent } from './pages/error-page/error-page.component';

const routes: Routes = [
  { path: '', component: WellcomeComponent },
  { path: 'אודות', component: AboutComponent },
  { path: 'מטרת-האתר', component: GoalComponent },
  { path: 'בלוג', component: BlogComponent },
  { path: 'צור-קשר', component: ContactComponent },
  { path: 'לקוחות', component: CustomersComponent },
  // { path: 'אולם-אירועים', component: HallsComponent },
  {
    path: "אולם-אירועים", component: HallsComponent, resolve: { halls: HallResolver }, children: [
      {
        path: ":id", component: HallComponent, children: [
          { path: "מדיה", component: HallMediaComponent },
          { path: 'אודות', component: HallAboutComponent }
        ]},
    ]},
  {
    path: "סלון-כלות", component: SalonsComponent, children: [
      {
        path: ":id", component: SalonComponent, children: [
          { path: "ערוך", component: HallEditComponent },
          { path: "מדיה", component: SalonMediaComponent },
          { path: 'אודות', component: SalonAboutComponent }
        ]},
    ]},

  { path: 'די-גי', component: DjsComponent },
  { path: 'צלם-אירועים', component: PhotograpersComponent, children:[
    { path: ":id", component: PhotographerComponent },
    { path: ":id/about", component: photographerAboutComponent }
  ] },
  { path: 'בתי-דפוס', component: PrintingComponent },
  { path: 'הסעות', component: TransportationComponent },
  { path: 'השכרת-רכב', component: TransportationComponent, children : [
    { path: ':id', component: CarRentComponent },
    { path: ':id/about', component: CarRentAboutComponent }

  ] },
  { path: 'בתי-מלון', component: HotelsComponent },
  { path: 'קייסים-רבנות', component: RabbinateComponent },
  // { path: "**", component: PageNotFoundComponent}
  { path: "errors-page", component: ErrorPageComponent, data: {errorMsg:"You have page ERRoR"}},
  { path: "**",  redirectTo: "/errors-page"}
  
];
  

@NgModule({
  imports: [
      RouterModule.forRoot(routes)
    ],
  exports: [RouterModule]
})
export class PagesRoutingModule{}