import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, RoutesRecognized } from '@angular/router';
import { map, filter } from 'rxjs/operators';

@Component({
  selector: 'app-salons',
  templateUrl: './salons.component.html',
  styleUrls: ['./salons.component.css']
})
export class SalonsComponent implements OnInit, OnDestroy {

  path: boolean = true;
  ondes: any = "";
  
  constructor(private router: Router) { }

  ngOnInit() {

    let pt = decodeURIComponent(window.location.pathname).slice(1) === 'סלון-כלות' ? true : false;
    
    this.ondes = this.router.events.pipe(
      filter(evt => evt instanceof RoutesRecognized),
      map(_evt => decodeURIComponent(_evt["url"]).slice(1)),
    )
    .subscribe(evt => {
      this.path = (evt === 'סלון-כלות') ? true : false;
    });
    this.path = pt;

  }
  ngOnDestroy() {
    this.ondes.unsubscribe();
  }
}
